<template>
  <div id="app">
    <div class="div1">
      <!-- 尺寸 -->
      <div class="ys">印刷计价</div>
      <div class="fa">
        <div class="div11">
          <div>
            尺寸：&nbsp;&nbsp;&nbsp;&nbsp;
            长度：
            <el-input v-model="long" placeholder="请输入长度" @blur="longchange()"></el-input>&nbsp;&nbsp;毫米&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            宽度：
            <el-input v-model="short" placeholder="请输入宽度"></el-input>&nbsp;&nbsp;毫米
          </div>
          <!-- 数量 -->
          <div>
            数量：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-input v-model="nums" placeholder="请输入数量"></el-input>&nbsp;&nbsp;个
          </div>
          <!-- 纸张 材质。大小。克重 -->
          <div>
            纸张：&nbsp;&nbsp;&nbsp;&nbsp;
            规格：
            <!-- <el-input v-model="nums" placeholder="请输入规格"></el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; -->
            <el-select v-model="value" placeholder="请选择" @change="changevalue()">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;克重：
            <el-input v-model="weight" placeholder="请输入克重"></el-input>&nbsp;&nbsp;克/平米
          </div>
          <!-- 进价 -->
          <div>
            进价：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <el-input v-model="money" placeholder="请输入数量"></el-input>&nbsp;&nbsp;元/吨
          </div>
        </div>
        <div class="div12">
          <div>
            <!-- <el-button type="primary">查询</el-button>
            <el-button type="danger" @click="reset()">重置</el-button>-->
          </div>
          <div>
            计价公式：
            <span
              v-if="value=='对开'&&nums&&money&&weight&&long&&short"
            >{{'900(长)'+'×'+'600(宽)'+'÷'+'1000000(转换平方米)'+'×'+nums+'(个数)×'+money+'(进价)×'+weight+'(克重)'+'÷'+'1000000(转换吨)'}}</span>
            <span
              v-if="value=='四开'&&nums&&money&&weight&&long&&short"
            >{{'740(长)'+'×'+'530(宽)'+'÷'+'1000000(转换平方米)'+'×'+nums+'(个数)×'+money+'(进价)×'+weight+'(克重)'+'÷'+'1000000(转换吨)'}}</span>
            <span
              v-if="value=='八开'&&nums&&money&&weight&&long&&short"
            >{{'520(长)'+'×'+'370(宽)'+'÷'+'1000000(转换平方米)'+'×'+nums+'(个数)×'+money+'(进价)×'+weight+'(克重)'+'÷'+'1000000(转换吨)'}}</span>
          </div>
          <div>
            总价：
            <span
            class="respan"
              v-if="value=='对开'&&nums&&money&&weight&&long&&short"
            >￥{{900*600/1000000*nums*money*weight/1000000}}元</span>
            <span
            class="respan"
              v-if="value=='四开'&&nums&&money&&weight&&long&&short"
            >￥{{740*530/1000000*nums*money*weight/1000000}}元</span>
            <span
            class="respan"
              v-if="value=='八开'&&nums&&money&&weight&&long&&short"
            >￥{{520*370/1000000*nums*money*weight/1000000}}元</span>
          </div>
          <div>
            单价：
            <span
            class="respan"
              v-if="value=='对开'&&nums&&money&&weight&&long&&short"
            >￥{{900*600/1000000*money*weight/1000000}}元</span>
            <span
            class="respan"
              v-if="value=='四开'&&nums&&money&&weight&&long&&short"
            >￥{{740*530/1000000*money*weight/1000000}}元</span>
            <span
            class="respan"
              v-if="value=='八开'&&nums&&money&&weight&&long&&short"
            >￥{{520*370/1000000*money*weight/1000000}}元</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      long: 0,
      short: 0,
      nums: null,
      value: null,
      money: null,
      weight: null,
      options: [
        {
          value: "对开",
          label: "对开"
        },
        {
          value: "四开",
          label: "四开"
        },
        {
          value: "八开",
          label: "八开"
        }
      ]
    };
  },
  watch: {
    long() {
      let _that = this;
      if (this.short <= 370 && this.long <= 520) {
        _that.value = "八开";
      } else if (this.short <= 370 && this.long <= 740 && this.long > 520) {
        _that.value = "四开";
      } else if (this.short <= 370 && this.long <= 900 && this.long > 740) {
        _that.value = "对开";
      } else if (this.short <= 530 && this.short >= 370 && this.long <= 740) {
        _that.value = "四开";
      } else if (this.short <= 530 && this.short >= 370 && this.long > 740) {
        _that.value = "对开";
      } else if (this.short <= 600 && this.short >= 530) {
        _that.value = "对开";
      }
    },
    short() {
      let _that = this;
      if (this.long <= 520 && this.short <= 370) {
        _that.value = "八开";
      } else if (this.long <= 520 && this.short <= 530 && this.short > 370) {
        _that.value = "四开";
      } else if (this.long <= 520 && this.short <= 600 && this.short > 530) {
        _that.value = "对开";
      } else if (this.long <= 740 && this.long >= 520 && this.short <= 530) {
        _that.value = "四开";
      } else if (this.long <= 740 && this.long >= 520 && this.short > 530) {
        _that.value = "对开";
      } else if (this.long <= 900 && this.long >= 740) {
        _that.value = "对开";
      }
    }
  },
  methods: {
    reset() {
      (this.long = null),
        (this.short = null),
        (this.nums = null),
        (this.value = null),
        (this.money = null),
        (this.weight = null);
    },
    changevalue() {
      if (this.value == "对开") {
        this.long = 900;
        this.short = 600;
      }
      if (this.value == "四开") {
        this.long = 740;
        this.short = 530;
      }
      if (this.value == "八开") {
        this.long = 520;
        this.short = 370;
      }
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.div1 {
  width: 80%;
  padding-left: 2%;
  border: 1px solid #ccc;
}
.ys {
  font-size: 1.25rem;
  font-weight: 700;
}
.fa {
  display: flex;
  padding-bottom: 10px;
}

.el-input {
  width: 12.5rem!important;
}
div {
  margin-top: 0.3125rem;
}
.div11 {
  width: 50%;
}
.div12 {
  padding-top: 1%;
  width: 50%;
}
.respan{
  color: red;
  font-size: 1.25rem;
}
</style>



